import React from 'react';
import { Popover, Typography } from 'antd';
import styled from 'styled-components';
import { MeI } from '../../types/Me';
import PopoverContent from './PopoverContent';
import Avatar from './Avatar';

const TopAvatar = styled(Avatar)`
  &&& {
    height: 24px;
    width: 24px;
    vertical-align: middle;
    margin-right: 4px;
  }
`;

const { Text } = Typography;

const Name = styled(Text)`
  &&& {
    max-width: 150px;
    vertical-align: middle;
  }
`;

interface Props {
  me: MeI;
}

export default ({ me }: Props) => (
  <Popover placement="bottom" autoAdjustOverflow={false} content={<PopoverContent me={me} />}>
    <TopAvatar path={me.additionalInformation?.avatar} />
    <Name ellipsis>{me.realName}</Name>
  </Popover>
);
