import React, { useCallback, useContext } from 'react';
import register from '../function/register';
import LoginOrRegisterContext from './LoginOrRegisterContext';

export default () => {
  const args = useContext(LoginOrRegisterContext);

  const callRegister = useCallback(() => {
    register(args);
  }, [args]);

  return (
    <a className="register" onClick={callRegister}>
      注册
    </a>
  );
};
