import login, { LoginArgs } from '../login/login';
import requestOpBaseUrl from '../requestOpBaseUrl';

import doLogout from './doLogout';

export interface LogoutSelfArgs {
  /**
   * 注销后的跳转地址
   */
  logoutRedirectUrl?: string;

  /**
   * 注销后的跳转地址目标窗口
   */
  logoutRedirectTarget?: string;

  /**
   * 注销其他的第三方单点登录, 例如政采云
   */
  otherLogoutUrls?: Array<string>;
}

export interface LogoutArgs extends LoginArgs, LogoutSelfArgs {}

export default async ({
  logoutRedirectUrl,
  logoutRedirectTarget,
  otherLogoutUrls,
  ...loginArgs
}: LogoutArgs) => {
  try {
    const opBaseUrl = await requestOpBaseUrl(loginArgs.origin, loginArgs.djcGatewayBaseUrl);

    await doLogout(opBaseUrl, otherLogoutUrls);
  } finally {
    if (logoutRedirectUrl) {
      /**
       * 跳转到指定页面
       */
      window.open(logoutRedirectUrl, logoutRedirectTarget || '_top');
    } else {
      // 重新登录
      await login(loginArgs);
    }
  }
};
