import { WHO_AM_I_HOST_KEY } from '../../constants/whoAmIHostKey';
import parseUrl from '../../utils/parseUrl';
import { LoginBySSOArgs } from '../../function/login/loginBySSO';
import { loginBySSO } from '../../function';
import { cacheFlush, cachePush } from './Cache';

/**
 * 重新发起请求
 */
const reFetch = (fetcher: any, response: any, afterRetry?: Function) =>
  fetcher({
    ...response.config,
    SSOInterceptorIgnore: true,
  }).then((res: any) => {
    if (afterRetry) afterRetry();
    return res;
  });

interface LoadingI {
  [key: string]: boolean;
}

/**
 * 优化性能, 如果有多个401请求并发, 只执行第一个
 */
const loading: LoadingI = {};

/**
 * 触发并发请求的重试
 */
function retryCache(key: string) {
  cacheFlush(key)(item => {
    const { response, resolve, fetcher, afterRetry } = item;
    resolve(reFetch(fetcher, response, afterRetry));
  });
}

/**
 * 重试失败返回 response
 */
function rejectCache(key: string) {
  cacheFlush(key)(item => {
    const { response, reject, afterRetry } = item;
    reject(response);
    if (afterRetry) afterRetry();
  });
}

// 登录并重新发起请求
async function loginAndRetry(
  args: LoginBySSOArgs,
  response: any,
  fetcher: any,
  afterRetry?: Function
) {
  /**
   * 获取请求的域, 尝试单点登录此域
   */
  const { origin } = parseUrl(response.config.url);
  const whoAmIHost =
    response.config && response.config.params && response.config.params[WHO_AM_I_HOST_KEY]
      ? response.config.params[WHO_AM_I_HOST_KEY]
      : parseUrl(origin).host;
  const key = `${origin}+${whoAmIHost}`;

  /**
   * 优化性能, 如果有相同域的单点登录请求合并为一个
   */
  if (loading[key]) {
    return new Promise((resolve, reject) => {
      cachePush(key)({ response, resolve, reject, fetcher, afterRetry });
    });
  }
  /**
   * 尝试单点登录
   */
  try {
    loading[key] = true;
    await loginBySSO({ ...args, origin, whoAmIHost });
  } catch (e) {
    rejectCache(key);
    return Promise.reject(response);
  } finally {
    loading[key] = false;
  }

  /**
   * 登录成功再次发起请求(缓存的相同登录域的请求)
   */
  retryCache(key);

  /**
   * 登录成功再次发起请求
   */
  return reFetch(fetcher, response, afterRetry);
}

export default loginAndRetry;
