import request from '../../utils/request';
import { AUTHORIZE_URL } from '../../constants/url';

const authorize = async (opBaseUrl: string, params: object) => {
  const response = await request(AUTHORIZE_URL(opBaseUrl), {
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/x-www-form-urlencoded',
    },
    params: {
      ...params,
      tenantId: 'gmsoft-web',
    },
  });

  return response?.data || {};
};

export default authorize;
