import React, { useContext } from 'react';
import { Menu, Typography } from 'antd';
import styled from 'styled-components';
import { MeI } from '../../types/Me';
import { ANTD_PREFIX } from '../../constants/antdPrefix';
import Avatar from './Avatar';
import Logout from './Logout';
import LoginOrRegisterContext from '../LoginOrRegisterContext';

const { Paragraph } = Typography;

const Wrap = styled.div`
  &&& {
    max-width: 280px;
    margin-bottom: -6px;
    .${ANTD_PREFIX}-menu-vertical {
      border-right: none;
      .${ANTD_PREFIX}-menu-item {
        cursor: default;
        line-height: 35px;
        height: auto;
        text-align: center;
        margin: 0;
        &.${ANTD_PREFIX}-menu-item-selected, &:active {
          background-color: white;
        }
      }
    }
  }
`;

const UserAvatar = styled(Avatar)`
  &&& {
    height: 64px;
    width: 64px;
    vertical-align: top;
    border: 1px solid #dedede;
  }
`;

const P = styled(Paragraph).attrs({
  ellipsis: true,
})`
  &&& {
    margin: 0;
  }
`;

const Name = styled(P)`
  font-size: 16px;
  font-weight: 700;
`;

interface Props {
  me: MeI;
}

export default ({ me }: Props) => {
  const { toCenterPage } = useContext(LoginOrRegisterContext);
  return (
    <Wrap>
      <Menu mode="vertical">
        <Menu.Item key="avatar">
          <UserAvatar path={me.additionalInformation?.avatar} />
        </Menu.Item>
        <Menu.Item key="name">
          {me.realName && (
            <a onClick={toCenterPage}>
              <Name>{me.realName}</Name>
            </a>
          )}
          {me.org?.orgName && <P>{me.org?.orgName}</P>}
        </Menu.Item>
        <Menu.Divider />
        <Menu.Item key="logout">
          <Logout />
        </Menu.Item>
      </Menu>
    </Wrap>
  );
};
