import React, { useCallback, useContext } from 'react';
import login, { LoginArgs } from '../function/login/login';
import LoginOrRegisterContext from './LoginOrRegisterContext';

export default () => {
  const args = useContext(LoginOrRegisterContext);
  const callLogin = useCallback(() => {
    login(args as LoginArgs);
  }, [args]);

  return (
    <a className="login" onClick={callLogin}>
      登录
    </a>
  );
};
