const TOKEN_CACHE = '__token__';
const TOKEN_PARAMETER_KEY_CACHE = '__token_param_key__';
const DEFAULT_TOKEN_PARAMETER_KEY = '__token__';

/**
 * 写缓存
 */
export const save = (tokenParamKey: string, token: string) => {
  try {
    if (tokenParamKey && token) {
      /**
       * 缓存 tokenParamKey, token 目的:
       * 1. 前端路由时不用携带 token
       * 2. 页面跳转时携带 token (for function getTokenCacheWithKey)
       */
      window[TOKEN_PARAMETER_KEY_CACHE] = tokenParamKey;
      window[TOKEN_CACHE] = token;

      /**
       * 备用缓存方案, 尝试缓存到 history.state 中
       * 避免页面刷新导致的丢失 token 的情况
       */
      window.history.replaceState(
        {
          ...window.history.state,
          [TOKEN_PARAMETER_KEY_CACHE]: tokenParamKey,
          [TOKEN_CACHE]: token,
        },
        ''
      );
    }
  } catch {
    // ignore cache failed
  }
};

/**
 * 读取缓存的 tokenParamKey
 */
export const getTokenParamKey = () => {
  try {
    return (
      window[TOKEN_PARAMETER_KEY_CACHE] ||
      window.history.state[TOKEN_PARAMETER_KEY_CACHE] ||
      DEFAULT_TOKEN_PARAMETER_KEY
    );
  } catch {
    return DEFAULT_TOKEN_PARAMETER_KEY;
  }
};

/**
 * 读取缓存的 token
 */
export const getToken = () => {
  try {
    return window[TOKEN_CACHE] || window.history.state[TOKEN_CACHE];
  } catch {
    return null;
  }
};
