import { AnyAction, Dispatch } from 'redux';
import { CompatibleMeI } from '../types/Me';

const SET_GLOBAL_CONTEXT = '@@GLOBAL_CONTEXT/SET';

function setGlobalContextMe(dispatch?: Dispatch<AnyAction>, me?: CompatibleMeI) {
  if (dispatch) {
    dispatch({
      type: SET_GLOBAL_CONTEXT,
      payload: { me },
    });
  }
}

export default setGlobalContextMe;
