import { LoginBySSOArgs } from '../../function/login/loginBySSO';
import loginAndRetry from './loginAndRetry';

/**
 * 触发scope脏值检测
 */
const scopeApply = ($injector: any) => () => {
  const $rootScope = $injector.get('$rootScope');
  const $timeout = $injector.get('$timeout');

  $timeout(() => {
    if ($rootScope && !$rootScope.$$phase) $rootScope.$apply();
  });
};

function SSOInterceptor(args: LoginBySSOArgs) {
  window.angular
    .module('SSOInterceptor', [])
    .factory('SSOInterceptor', [
      '$injector',
      '$q',
      function interceptor($injector: any, $q: any) {
        return {
          responseError(response: any) {
            // ignore 401
            if (!response || !response.config || response.config.SSOInterceptorIgnore) {
              return $q.reject(response);
            }

            if (response && response.status === 401) {
              return loginAndRetry(args, response, $injector.get('$http'), scopeApply($injector));
            }

            return $q.reject(response);
          },
        };
      },
    ])
    .config([
      '$httpProvider',
      function config($httpProvider: any) {
        $httpProvider.interceptors.push('SSOInterceptor');
      },
    ]);

  return 'SSOInterceptor';
}

export default SSOInterceptor;
