import login from './login';
import { LoginCommonArgs } from './types';

export interface LoginByPageArgs extends LoginCommonArgs {
  /**
   * 登录页面地址
   * mode = page, 必须提供
   * 此页面会接受到 state 参数, 必须传递给 oauth-login 页面
   */
  loginPage: string;
}

export default (args: LoginByPageArgs) => login({ ...args, mode: 'page' });
