import React from 'react';
import { AnyAction, Dispatch } from 'redux';
import { Props } from './LoginOrRegister';

type LoginOrRegisterContextI = Omit<Props, 'className'> & {
  /**
   * 用于设置全局状态
   */
  dispatch: Dispatch<AnyAction>;
};

/**
 * 配置环境
 */
export default React.createContext<LoginOrRegisterContextI>({
  mode: 'page',
  authPageOrigin: '',
  loginPage: '',
  djcGatewayBaseUrl: '',
  loginSuccessRedirectUri: '/login-app/login-success/index.html',
  toCenterPage: () => undefined,
  dispatch: action => action,
});
