import request from '../../utils/request';
import { LOGIN_URL, PLAT_INFO_URL } from '../../constants/url';
import parseUrl from '../../utils/parseUrl';
import { LoginSuccessRedirectTargetI } from './types';
import OrgIdentityType from '../../enums/OrgIdentityType';

/**
 * 登录目标域应用id
 * 如果传递了平台id和部署点约定使用户 ${platId}@${dp} 作为 clientId
 */
const getClientId = (platId: string, dp?: string, clientId?: string) => {
  if (clientId) {
    return clientId;
  }
  if (dp) {
    return `${platId}@${dp}`;
  }
  return `plat@${platId}`;
};

/**
 * 登录成功跳转地址
 */
const getLoginSuccessRedirectUrl = (origin?: string, loginSuccessRedirectUri?: string) => {
  if (loginSuccessRedirectUri) return loginSuccessRedirectUri;
  return `${origin || window.location.origin}/login-app/login-success/index.html`;
};

export interface BuildLoginParamsArgs {
  /**
   * 查询平台信息网关
   */
  djcGatewayBaseUrl: string;

  /**
   * 登录目标域
   */
  origin?: string;

  /**
   * who am i, 默认为 origin -> host
   */
  whoAmIHost?: string;
  // typo, 为了兼容而保留
  whoAmIhost?: string;

  /**
   * 平台id, 仅限内网使用
   */
  platId?: string;

  /**
   * 部署点, 仅限内网使用
   */
  dp?: string;

  /**
   * 授权页面UI
   */
  ui?: string;

  /**
   * 跳转到注册页面是否传递 noHeader 参数
   * @default false
   */
  registerNoHeader?: boolean;

  /**
   * 页面title
   */
  title?: string;

  /**
   * 登录目标域应用id
   */
  clientId?: string;

  /**
   * 登录成功跳转地址
   */
  loginSuccessRedirectUri?: string;

  /**
   * 登录成功或失败后的跳转目标
   * 失败时 mode = popup 时, 此参数有效
   */
  loginSucessRedirectTarget?: LoginSuccessRedirectTargetI;

  /**
   * 限制登录用户的身份类型
   */
  limitOrgTypes?: Array<OrgIdentityType>;

  /**
   * 错误信息
   */
  errorMessage?: string;
}

const buildParams = async ({
  origin = window.location.origin,
  whoAmIHost,
  // typo, 为了兼容而保留
  whoAmIhost,
  djcGatewayBaseUrl,
  platId,
  dp,
  ui,
  registerNoHeader,
  title,
  clientId,
  loginSuccessRedirectUri,
  loginSucessRedirectTarget,
  limitOrgTypes,
  errorMessage,
}: BuildLoginParamsArgs) => {
  const originHost = parseUrl(origin).host;

  let _platId = platId;

  // 如果传递了 platId 不查询平台信息
  if (!platId) {
    const { data: platInfo } = await request(PLAT_INFO_URL(djcGatewayBaseUrl), {
      params: {
        orgDomainName: originHost,
      },
    });

    _platId = platInfo?.platformCode;
  }

  if (!_platId) {
    const error = `未查询到 ${originHost} 对应的平台信息, 请检查平台配置是否正确`;

    // eslint-disable-next-line no-console
    console.log(error);

    return Promise.reject(new Error(error));
  }

  return {
    ui,
    register_no_header: registerNoHeader,
    title,
    client_id: getClientId(_platId, dp, clientId),
    // typo whoAmIhost, 为了兼容而保留
    scope: whoAmIHost || whoAmIhost || originHost,
    login_sucess_redirect_uri: getLoginSuccessRedirectUrl(origin, loginSuccessRedirectUri),
    login_sucess_redirect_target: loginSucessRedirectTarget,
    redirect_uri: LOGIN_URL(origin),
    is_fpa: true,
    error_message: errorMessage,
    options: limitOrgTypes && {
      limitOrgTypes,
    },
  };
};

export default buildParams;
