import axios from 'axios';
import { LoginBySSOArgs } from '../../function/login/loginBySSO';
import loginAndRetry from './loginAndRetry';

function SSOInterceptor(args: LoginBySSOArgs) {
  return (error: any) => {
    // ignore 401
    if (!error || !error.config || error.config.SSOInterceptorIgnore) {
      return Promise.reject(error);
    }

    if (error && error.response && error.response.status === 401) {
      /**
       * 此处传递 error 作为参数
       * 因为可能有直接把 error reject 的情况
       */
      return loginAndRetry(args, error, axios);
    }

    return Promise.reject(error);
  };
}

export default SSOInterceptor;
