interface CacheItemI {
  response: any;
  resolve: any;
  reject: (reason?: any) => void;
  fetcher: any;
  afterRetry?: Function;
}

interface CacheI {
  [key: string]: Array<CacheItemI>;
}

interface FlushFnI {
  (item: CacheItemI): void;
}

const cache: CacheI = {};

const cachePush = (key: string) => (item: CacheItemI) => {
  if (!cache[key]) {
    cache[key] = [];
  }
  cache[key].push(item);
};

const cacheFlush = (key: string) => (fn: FlushFnI) => {
  while (cache[key] && cache[key].length > 0) {
    const item = cache[key].shift();
    if (item) {
      fn(item);
    }
  }
};

export { cachePush, cacheFlush };
