import React, { MutableRefObject } from 'react';
import ReactDOM from 'react-dom';
import styled from 'styled-components';
import Modal from '../../components/Modal';
import { ANTD_PREFIX } from '../../constants/antdPrefix';

let cancelLogin: () => void = () => undefined;

const LoginBoxModal = styled(Modal)`
  &&& .${ANTD_PREFIX}-modal-body {
    border: 3px solid #666666;
    border-radius: 3px;
    padding: 0;
  }
`;

const popupLoginBox = (
  loginFormUrl: string,
  onCancelLogin?: () => void,
  closePopupRef?: MutableRefObject<Function>
) => {
  if (cancelLogin) cancelLogin();

  const div = document.createElement('div');
  document.body.appendChild(div);

  const close = () => ReactDOM.unmountComponentAtNode(div);

  cancelLogin = () => {
    if (onCancelLogin && typeof onCancelLogin === 'function') {
      onCancelLogin();
    }
    close();
  };

  ReactDOM.render(
    <LoginBoxModal
      visible
      maskClosable
      closable={false}
      footer={null}
      closeIcon={null}
      onCancel={cancelLogin}
      width={366}
    >
      <iframe
        title="loginBox"
        className="login-form-box"
        src={loginFormUrl}
        width="360px"
        height="440px"
        scrolling="no"
        style={{ margin: 0, padding: 0, display: 'block' }}
        frameBorder="0"
      />
    </LoginBoxModal>,
    div
  );

  // eslint-disable-next-line no-param-reassign
  if (closePopupRef) closePopupRef.current = close;

  return close;
};

export default popupLoginBox;
