import parseUrl from '../utils/parseUrl';

export const ME_URL = (origin: string = window.location.origin) =>
  `${origin}/gateway/v1/user?requireAvatar=true`;

export const LOGIN_URL = (origin: string = window.location.origin) => `${origin}/gateway/v1/login`;

export const OP_GET_URL = (origin: string = window.location.origin) => {
  const { host } = parseUrl(origin);
  return `${origin}/gateway/v1/op?host=${encodeURIComponent(host)}`;
};

export const AUTHORIZE_URL = (opBaseUrl: string) => `${opBaseUrl}/oauth/authorize`;

export const PLAT_INFO_URL = (djcGatewayBaseUrl: string) =>
  `${djcGatewayBaseUrl}/djcsupport/domainname/getPlatformConfigure`;
