import qs from 'qs';
import * as cache from './cache';

/**
 * 从 url 参数中获取 token
 * 并更新缓存
 */
export default (tokenParamKey?: string) => {
  const query = qs.parse(window.location.search, { ignoreQueryPrefix: true }) || {};

  const tpk = tokenParamKey || cache.getTokenParamKey();

  /**
   * 1. 从请求参数中获取 token
   * 2. 如果参数中没有 token, 尝试从缓存中获取
   */
  const token = query[tpk] || cache.getToken();

  cache.save(tpk, token);

  return token;
};
