import { Dispatch, AnyAction } from 'redux';
import login from './login';

export interface LoginBySSOArgs {
  /**
   * 大家采网关地址
   * 用于获取平台信息
   */
  djcGatewayBaseUrl: string;

  /**
   * 用于设置全局状态
   */
  dispatch: Dispatch<AnyAction>;

  /**
   * 登录目标域
   */
  origin?: string;

  /**
   * who am i, 默认为 origin -> host
   */
  whoAmIHost?: string;
  // typo, 为了兼容而保留
  whoAmIhost?: string;

  /**
   * 平台 id, 仅限内网使用
   */
  platId?: string;

  /**
   * 指定部署点 例如 PM, 仅限内网使用
   */
  dp?: string;

  /**
   * 登录目标域应用id
   * 未指定, 使用 约定格式: “plat@平台id”
   */
  clientId?: string;

  /**
   * 其他state参数, 可以通过次对象传递给应用的登录接口
   */
  oauthState?: object;
}

export default (args: LoginBySSOArgs) => login({ ...args, mode: 'sso' });
