import request from '../../utils/request';
import reflect from '../../utils/promise-reflect';
import flatDeep from '../../utils/flatDeep';
import requestByImage from '../../utils/requestByImage';

interface ResultI {
  data: {
    apps: Array<string>;
    ops: Array<string>;
  };
}

const mapUrls = (urls: Array<string> = []) => {
  const promises = urls.filter(url => url).map((url: string) => requestByImage(url));
  // 等待所有注销调用完成
  return Promise.all(promises.map(reflect));
};

/**
 * 登录注销
 */
const doLogout = async (opBaseUrl: string, otherLogoutUrls?: Array<string>) => {
  const { data } = await request(`${opBaseUrl}/logout`, {
    method: 'DELETE',
  });

  let urls = data.apps || [];

  /**
   * 注销其他认证中心
   */
  if (data.ops && data.ops.length > 0) {
    const promises = data.ops.map((op: string) =>
      request(op, {
        method: 'DELETE',
        // 静默失败
      }).catch(() => undefined)
    );

    const results = await Promise.all<ResultI>(promises);

    if (results && results.length > 0) {
      urls = urls.concat(flatDeep(results.map((result: ResultI) => result?.data?.apps)));
    }
  }

  /**
   * 注销其他的第三方单点登录, 例如政采云
   */
  if (otherLogoutUrls && otherLogoutUrls.length > 0) {
    urls = urls.concat(otherLogoutUrls);
  }

  /**
   * 处理异常情况, 如果注销失败, 第二次调用认证中心的接口会返回空列表
   * 在此检查保证始终调用访问域名的注销接口
   */
  const currentOriginLogoutUrl = `${window.location.origin}/gateway/v1/logout`;
  if (!urls.includes(currentOriginLogoutUrl)) urls.push(currentOriginLogoutUrl);

  await mapUrls(urls);
};

export default doLogout;
