import qs from 'qs';
import UIType from '../enums/UIType';
import request from '../utils/request';
import { PLAT_INFO_URL } from '../constants/url';
import parseUrl from '../utils/parseUrl';

export interface RegisterArgs {
  /**
   * 授权页面 origin, 注册也部署在此域下
   */
  authPageOrigin: string;
  /**
   * 大家采网关地址
   * 用于获取平台信息
   */
  djcGatewayBaseUrl: string;
  /**
   * 默认使用当前访问域名去查询对应的平台信息, 可以通过此参数指定域名
   * 例如 https://www.gec123.com
   */
  origin?: string;
  /**
   * 如果指定了ui, 则使用该值, 否则从 origin 和 dp 推断 ui
   */
  ui?: UIType;
  /**
   * 是否隐藏页头
   * @default false
   */
  noHeader?: boolean;
  /**
   * @部署点 例如: PM
   */
  dp?: string;
}

export default async ({
  authPageOrigin,
  origin,
  ui,
  dp,
  djcGatewayBaseUrl,
  noHeader,
}: RegisterArgs) => {
  // 注销, 然后跳转到登录页面
  const registerUrl = `${authPageOrigin}/register/select-type`;

  let platId: string | null = null;

  const { data: platInfo } = await request(PLAT_INFO_URL(djcGatewayBaseUrl), {
    params: {
      orgDomainName: origin ? parseUrl(origin).host : window.location.host,
    },
  });

  platId = platInfo?.platformCode;

  window.open(
    `${registerUrl}${qs.stringify(
      {
        ui,
        platId,
        dp,
        noHeader,
      },
      { addQueryPrefix: true, encode: false }
    )}`,
    '_blank'
  );
};
