enum OrgIdentityType {
  PLATFORM = 1,
  APPROVAL = 2,
  STOCK = 3,
  AGENT = 4,
  PROVIDER = 5,
  FINANCE = 7,
  EMPLOYEE = 101,
  EXPERT = 102,
  TEACHER = 103,
}

export default OrgIdentityType;

export const getOrgIdentityString = (type?: OrgIdentityType) => {
  switch (type) {
    case OrgIdentityType.PLATFORM:
      return 'platform';
    case OrgIdentityType.APPROVAL:
      return 'approval';
    case OrgIdentityType.STOCK:
      return 'stock';
    case OrgIdentityType.AGENT:
      return 'agent';
    case OrgIdentityType.PROVIDER:
      return 'provider';
    case OrgIdentityType.FINANCE:
      return 'finance';
    case OrgIdentityType.EMPLOYEE:
      return 'employee';
    case OrgIdentityType.EXPERT:
      return 'expert';
    case OrgIdentityType.TEACHER:
      return 'teacher';
    default:
      return '';
  }
};

