import { CompatibleMeI } from '../types/Me';
import loginBySSO, { LoginBySSOArgs } from './login/loginBySSO';

const requestMeTrySSO = async (args: LoginBySSOArgs) => {
  const me = await loginBySSO(args);
  if (!me) return Promise.reject();
  return me as unknown as CompatibleMeI;
};

export default requestMeTrySSO;
