import React, { useEffect } from 'react';
import { useDispatch } from 'react-redux';
import { Typography } from 'antd';
import styled from 'styled-components';
import { requestMeTrySSO } from '../function';
import { LoginArgs } from '../function/login/login';
import { RegisterArgs } from '../function/register';
import LoginOrRegisterContext from './LoginOrRegisterContext';
import Me from './Me';
import { LogoutSelfArgs } from '../function/logout';
import Login from './Login';
import Register from './Register';
import { CommonProps } from './CommonProps';
import useMe from '../hooks/useMe';

const { Text } = Typography;

const Or = styled(Text).attrs({
  type: 'secondary',
})`
  margin: 0 8px;
`;

type _LoginArgs = Omit<LoginArgs, 'mode' | 'dispatch'>;

export interface Props extends _LoginArgs, LogoutSelfArgs, RegisterArgs, CommonProps {
  mode: 'page' | 'popup';
}

const LoginOrRegister = ({ className, ...rest }: Props) => {
  const me = useMe();

  const dispatch = useDispatch();

  /**
   * 如果在全局状态中没有, 尝试请求一次
   */
  useEffect(() => {
    if (!me) {
      requestMeTrySSO({ djcGatewayBaseUrl: rest.djcGatewayBaseUrl, dispatch }).catch(() => {});
    }
  }, [me, dispatch, rest.djcGatewayBaseUrl]);

  return (
    <LoginOrRegisterContext.Provider value={{ dispatch, ...rest }}>
      <div className={className}>
        {me ? (
          <Me me={me} />
        ) : (
          <>
            <Login />
            <Or>或</Or>
            <Register />
          </>
        )}
      </div>
    </LoginOrRegisterContext.Provider>
  );
};

export default LoginOrRegister;
