import { OP_GET_URL } from '../constants/url';
import request from '../utils/request';

export default async (origin: string = window.location.origin, djcGatewayBaseUrl: string) => {
  if (import.meta.env.VITE_DEPLPY === 'PM') {
    return `${djcGatewayBaseUrl}/authing`;
  }

  const response = await request(OP_GET_URL(origin));

  return response?.data?.op;
};
