import { MutableRefObject } from 'react';
import { AxiosError } from 'axios';
import parseUrl from '../../utils/parseUrl';
import buildParams from './buildLoginParams';
import { LoginModeI, LoginCommonArgs } from './types';
import authorize from './authorize';
import popupLoginBox from './popupLoginBox';
import requestMe from '../requestMe';
import requestOpBaseUrl from '../requestOpBaseUrl';

const gotoLoginPage = (url?: string) => {
  if (!url) {
    throw new Error('mode =  page, 必须指定 loginPage');
  }

  window.open(url, '_top');
};

export interface LoginArgs extends LoginCommonArgs {
  /**
   * 登录模式
   */
  mode: LoginModeI;

  /**
   * 登录页面地址
   * mode = page, 必须提供
   */
  loginPage?: string;

  /**
   * 取消登录事件处理
   */
  onCancelLogin?: () => void;

  /**
   * 关闭登录弹层, 由于登录之后需要重定向到登录成功页面, 所以关闭动作只有由引用来调用
   */
  closePopupRef?: MutableRefObject<Function>;
}

const login = async ({
  mode,
  origin = window.location.origin,
  whoAmIHost,
  // typo, 为了兼容而保留
  whoAmIhost,
  clientId,
  platId,
  dp,
  ui,
  registerNoHeader,
  title,
  loginPage,
  djcGatewayBaseUrl,
  loginSuccessRedirectUri,
  loginSucessRedirectTarget = '_self',
  limitOrgTypes,
  oauthState,
  onCancelLogin,
  closePopupRef,
  dispatch,
}: LoginArgs) => {
  const originHost = parseUrl(origin).host;
  const _whoAmIhost = whoAmIHost || whoAmIhost || originHost;

  try {
    const me = await requestMe(dispatch, origin, _whoAmIhost);
    return me;
  } catch (e) {
    const params = await buildParams({
      origin,
      whoAmIHost: _whoAmIhost,
      djcGatewayBaseUrl,
      clientId,
      platId,
      dp,
      ui,
      registerNoHeader,
      title,
      loginSuccessRedirectUri,
      loginSucessRedirectTarget,
      limitOrgTypes,
    });

    const opBaseUrl = await requestOpBaseUrl(origin, djcGatewayBaseUrl);

    try {
      const { location, res } = await authorize(opBaseUrl, {
        response_type: 'code',
        client_id: params.client_id,
        redirect_uri: params.redirect_uri,
        scope: params.scope,
        options: params.options,
        state: JSON.stringify({ ...params, ...oauthState }),
      });

      if (res === 0) {
        const me = await requestMe(dispatch, origin, _whoAmIhost).catch(() => {
          // eslint-disable-next-line no-console
          console.error('服务器错误, 登录成功, 但获取登录用户信息失败!');
        });
        return me;
      }

      if (mode === 'page') {
        gotoLoginPage(loginPage);
      }

      if (mode === 'popup') {
        if (location) return popupLoginBox(location, onCancelLogin, closePopupRef);
        // eslint-disable-next-line no-console
        console.log('login error, mode=popup, authorize not response location');
      }

      if (mode === 'sso') {
        return Promise.reject(new Error('尝试单点登录失败'));
      }
    } catch (error) {
      const _error = error as AxiosError;

      const message =
        _error?.response?.data?.error_message ||
        _error?.response?.data?.message ||
        _error.message ||
        '服务器端错误';

      // eslint-disable-next-line no-console
      console.error(message, error);

      // 页面登录是即使 authorize 调用失败, 依然跳转
      if (mode === 'page') {
        gotoLoginPage(loginPage);
      }
    }
  }
};

export default login;
