import { AxiosRequestConfig } from 'axios';
import { getToken } from '../../utils/tokenUtils';
import { HeaderTokenArgs } from './typing';

export const DEFAULT_AUTH_HEADER_KEY = 'Authorization';

/**
 * 为了尽量减少对外部设定的依赖(主要是移动端 h5)
 * 例如 Android webview 需要设置支持第三方 cookies, 需要设置支持 localStorage
 * 避免使用以上的客户端存储方案
 * 通过 url 参数和 headers 传递用户 token
 */
export default (
  { tokenParamKey, authHeaderKey }: HeaderTokenArgs,
  config: AxiosRequestConfig = {}
) => {
  const token = getToken(tokenParamKey);

  if (token) {
    // add token into headers
    const headers = { ...config.headers, [authHeaderKey || DEFAULT_AUTH_HEADER_KEY]: token };

    return { ...config, headers };
  }

  return config;
};
