import { cacheTokenAndParamKey } from 'src/utils/tokenUtils';
import setHeaderToken from './setHeaderToken';
import { HeaderTokenArgs } from './typing';

function HTInterceptor(args: HeaderTokenArgs) {
  /**
   * 初始化时先缓存一次 token 和 paramKey, 避免页面跳转时查询缓存失败
   */
  cacheTokenAndParamKey(args.tokenParamKey);

  window.angular
    .module('HTInterceptor', [])
    .factory('HTInterceptor', () => ({
      request(config: any) {
        return setHeaderToken(args, config);
      },
    }))
    .config([
      '$httpProvider',
      function config($httpProvider: any) {
        $httpProvider.interceptors.push('HTInterceptor');
      },
    ]);

  return 'HTInterceptor';
}

export default HTInterceptor;
