import qs, { IStringifyOptions } from 'qs';
import getTokenCacheWithKey from './getTokenCacheWithKey';

interface jumpWithTokenArgs {
  /**
   * 跳转地址
   */
  url: string;
  /**
   * 页面跳转目标窗口
   * @default _self
   */
  target?: string;
  /**
   * 额外的需要拼接到 url 的参数
   */
  extraParams?: object;
  /**
   * qs stringify 选项
   * @see https://www.npmjs.com/package/qs
   */
  qsStringifyOptions?: IStringifyOptions;
}

/**
 * 页面跳转时携带 token
 * 必须配合 headerToken 拦截器使用
 */
export default ({ url, target = '_self', extraParams, qsStringifyOptions }: jumpWithTokenArgs) => {
  const tokenObject = getTokenCacheWithKey();

  if (tokenObject) {
    const [path, search] = url.split('?');

    const newSearch = qs.stringify(
      { ...qs.parse(search), ...extraParams, ...tokenObject },
      { arrayFormat: 'repeat', allowDots: true, ...qsStringifyOptions }
    );

    const newUrl = [path, newSearch].join('?');

    window.open(newUrl, target);
  }
};
