import getIsAdmin from './getIsAdmin';
import { CompatibleMeI, MeI } from '../../types/Me';

interface CompatibilityMeI {
  (me?: MeI): CompatibleMeI | undefined;
}

const compatibilityMe: CompatibilityMeI = me => {
  if (!me || Object.getOwnPropertyNames(me).length === 0) return undefined;
  return {
    admin: getIsAdmin(me?.roles),
    adminAreaId: me?.org?.areaId,
    adminAreaName: me?.org?.areaName,
    maintainPlatId: me?.additionalInformation?.mpi,
    orgId: me?.org?.orgId,
    orgName: me?.org?.orgName,
    orgType: me?.org?.orgType,
    userName: me?.realName,
    userTypes: me?.roles ? me?.roles.join(',') : undefined,
    deptRole: me?.additionalInformation?.deptRole,
    isBidDown: me?.additionalInformation?.isBidDown,
    isSj: me?.additionalInformation?.isSj,
    orgClassNo: me?.additionalInformation?.orgClassNo,
    organClass: me?.additionalInformation?.organClass,
    userType: me?.additionalInformation?.userType,
    ...me,
  };
};
export default compatibilityMe;
