import { Avatar } from 'antd';
import React, { useMemo, useContext } from 'react';
import defaultAvatar from '../../imgs/defaultAvatar.svg';
import LoginOrRegisterContext from '../LoginOrRegisterContext';

interface Props {
  path?: string;
  className?: string;
}

export default ({ path, className }: Props) => {
  const { djcGatewayBaseUrl } = useContext(LoginOrRegisterContext);

  const src = useMemo(() => {
    // return 'https://lh3.googleusercontent.com/ogw/ADGmqu9g6TEN3h27tpp82Ie88Pk4W9SoLs2CRauXbDvMYQ=s32-c-mo'

    if (path)
      // eslint-disable-next-line max-len
      return `${djcGatewayBaseUrl}/files?business=avatar&filePath=${path}&fileName=file&attachment=false&contentType=image/png`;
    return defaultAvatar;
  }, [djcGatewayBaseUrl, path]);

  return <Avatar className={className} src={src} />;
};
